
.landing_background { 
        background: url(/react_portfolio/static/media/coding2.053e654c.jpg) no-repeat center center fixed;
        background-size: cover;
        height: 100vh;
}

.img_overlay {
    background: rgba(0, 0, 0, .6);
    height: 100vh;
}

.lp_text {
    color: white;
    font-family: 'Inconsolata', monospace;
    font-size: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 90vh;
}

@media screen and (max-width: 760px) {
    .dropdown-menu {
        position: relative;
        z-index: 1;
    }

    .lp_text {
        position: absolute;
        padding: 0px 20px;
        font-size: 25px;
    }
}

.top_nav {
    font-size: 20px;
    margin: 40px;
    float: right;
}

.main_header {
    color: white;
}
.navbar {
   font-size: 20px;
   padding: 10px;
}

.navbar-brand {
    font-family: 'Kumar One Outline', cursive;
}

.nav-link {
    font-family: 'Inconsolata', monospace;
    color: white !important;
}
.aboutme_wrapper {
    min-height: 100vh;
    font-family: 'Inconsolata', monospace;
}

.about_heading {
    color: #3c3b7e;
    font-size: 50px;
    padding-top: 30px;
}

.about_cards {
    display: flex;
    flex-flow: row;
    justify-content: center;
}

.cards {
    font-family: 'Inconsolata', monospace;
    margin-top: 50px !important;
    margin: auto;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.12),
                0 2px 4px 0 rgba(0,0,0,0.08);
}

.card-heading {
    color: #3c3b7e;
    text-align: center;
}

.card-content {
    color: #181691;
}

.bottom_text {
    color: #181691;
    margin: 0px 3%;
    margin-bottom: 30px;
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.12),
    0 2px 4px 0 rgba(0,0,0,0.08);
}

@media screen and (max-width: 1025px) {
    .about_cards {
        display: flex;
        flex-flow: row wrap;
    }
}




.carousel_wrapper {
    margin: 20px;
}

.carousel_heading {
    font-size: 20px;
    background-color: #010050;
}

.carousel-item {
    max-width: 30vw;
    height: auto;
}

.carousel-caption {
    height: 80%;
    background-color: rgba(0, 0, 0, .8);
}

.carousel_link {
    background-color: rgba(0, 0, 0);
    margin: 0% 20%;
}

.carousel-control-prev-icon, .carousel-control-next-icon {
    background-color: rgba(0, 0, 0, .8);
}

@media screen and (max-width: 1580px) {
    .carousel-item {
        max-width: 100%;
        height: auto;
    }

    .carousel-caption {
        height: 80%;
        background-color: rgba(0, 0, 0, .8);
    }
}
.portfolio_wrapper {
    font-family: 'Inconsolata', monospace;
    background-color: #010030;
    min-height: 50vh;
}

.main_carousel {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly !important;
}

.portfolio_heading {
    font-family: 'Inconsolata', monospace;
    font-size: 50px;
    padding-top: 30px;
}

@media screen and (max-width: 1030px) {
    .main-carousel {
        flex-wrap: wrap;
    }

}
.contact_wrapper {
    background-color: #000000;
    background-image: url(data:image/png;base64,);
    min-height: 50vh;
    display: flex;
    flex-flow: row;
    justify-content: space-evenly !important;
}

.contact_info {
    background-color: black;
    border-radius: 5px;
    font-family: 'Inconsolata', monospace;
    max-width: 100%;
}

.contact_me {
    font-size: 50px;
    color: white;
    font-family: 'Inconsolata', monospace;
}

.contact_content, .contact_areas {
    font-size: 20px;
}

.spotify_embed {
    justify-content: center;
    text-align: center;
    color: white;
}

@media screen and (max-width: 1235px) {
    .contact_wrapper {
        flex-wrap: wrap;
    }

    .contact_info {
        margin-bottom: 20px;
    }

}


.app_footer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    background-color: black;
    min-height: 5vh;
}



